
<template>
  <div class="container shifted"><center>
    <h1 class="h1">
      Build Key
    </h1>
    
    <div v-if="buildTapped">
      <p> Building </p>
    </div>

    <table v-else>
      <tr><td>Diamond in wallet</td><td align="right"> {{ balanceStr }} </td></tr>
      <tr><td><input v-model="keycount" placeholder="Key count"></td><td align="right"> {{ costStr }} </td>
      <tr><td colspan="2"><button class="action" v-on:click="add1">+1</button><button class="action" v-on:click="add2">+2</button><button class="action" v-on:click="add5">+5</button><button class="action" v-on:click="add10">+10</button><button class="action" v-on:click="add100">+100</button></td></tr>
      <tr><td colspan="2" align="center"><button class="action" v-on:click="buildKey">Build Gamekey to become winner</button></td></tr>
    </table>
<p>If you use more than 100 Diamonds to build new Gamekey, there is {{ airdropChanceStr }} % chance to get some of {{ airdropPotStr }} </p>
    <button class="action" v-on:click="$router.go(-1)">Close</button>
  </center></div>
</template>

<script>
import Vue from 'vue';
import Amplify, { API } from 'aws-amplify';
import AmplifyStore from '../store/store'

const uuidv4 = require('uuid/v4');

export default {
  name: 'BuildKey',
  data () {
    return {
      diamond: 0,
      keycount: 1,
      buildTapped: false
    }
  },

  computed: {
    gameInfo: function() {
      return JSON.parse(sessionStorage.getItem('gameInfo'));
    },
    balanceStr: function() {
      return parseFloat(this.diamond).toFixed(4);
    },
    costStr: function() {
      return this.cost.toFixed(4);
    },
    cost: function() {
      return this.keycount * this.gameInfo.KeyPrice;
    },
    airdropChanceStr: function() {
      return (this.gameInfo.AirdropChance * 100).toFixed(2);
    },
    airdropPotStr: function() {
      return parseFloat(this.gameInfo.AirdropPot).toFixed(5);
    }
  },

  created() {
    console.log('!!!!!!!!!!!!!!!1');
    console.log(this.gameInfo);
    API.get('apiLocksmith', '/lwiUserInfo', {}).then(response => {
      console.log(response);
      this.diamond = response.Diamond;
    }).catch(error => {
      console.log(error)
    });
  },

  methods: {
    add1() {
      this.keycount ++;
    },
    add2() {
      this.keycount += 2;
    },
    add5() {
      this.keycount += 5;
    },
    add10() {
      this.keycount += 10;
    },
    add100() {
      this.keycount += 100;
    },
    async buildKey() {
      if (this.cost > this.diamond) {
        alert('No enough Diamond to build Gamekey');
        return;
      }
     
      this.buildTapped = true;

      const currentUser = await this.$Amplify.Auth.currentUserInfo(); 

      API.post('apiLocksmith', '/lwiBuildKey', {'body': {'Diamond': this.cost, 'TxnId': uuidv4(), 'UserName':currentUser.username}  }).then(response => {
        this.$router.push('/');
      }).catch(error => {
        alert('something went wrong');
        this.buildTapped = false;
        console.log(error)
      });
    }
  }
}
</script>



