
<template>
  <div class="container shifted"><center>
    <h1 class="h1">
      My QRCode
    </h1>
    <qrcode-vue :value="value" :size="size" level="H"></qrcode-vue>
    <button class="action" v-on:click="$router.go(-1)">Close</button>
  </center></div>
</template>

<script>
import Vue from 'vue';
import Amplify, { API } from 'aws-amplify';
import QrcodeVue from 'qrcode.vue';
import AmplifyStore from '../store/store'

export default {
  name: 'Stat',
  data () {
    return {
      value: '',
      size: 300
    }
  },

  components: {
    QrcodeVue
  },

  async mounted() {
    const currentUser = await this.$Amplify.Auth.currentUserInfo();
    console.log(currentUser);
    this.value = "lwi://lwi/user?id=" + currentUser.id + "&name=" + currentUser.username;
  }
}
</script>



