
<template>
  <div class="container shifted">
    <center>
      <h1 class="h1">
        Detail
      </h1>

      <div v-if="diamonds == -1">
        Loading
      </div>
      <table v-else>
        <tr><td>Pot down</td><td align="right">{{ downStr }} </td></tr>
        <tr><td>Airdrop award</td><td align="right">{{ awardStr }} </td></tr>
        <tr><td>Referral reward</td><td align="right">{{ rewardStr }}</td></tr>
        <tr><td>Gameshare dividend</td><td align="right">{{ dividendStr }} </button></td></tr>
      </table>
      <button class="action" v-on:click="$router.go(-1)">Close</button>

    </center>
  </div>
</template>

<script>
import Vue from 'vue';
import Amplify, { API } from 'aws-amplify';
import lwiconst from './lwi-const';

export default {
  name: 'Vault',

  computed: {
    gameInfo: function() {
      return JSON.parse(sessionStorage.getItem('gameInfo'));
    },
    userInfo: function() {
      return JSON.parse(sessionStorage.getItem('userInfo'));
    },
    downStr: function() {
      return parseFloat(this.userInfo.PotDown).toFixed(5);
    },
    awardStr: function() {
      return parseFloat(this.userInfo.Award).toFixed(5);
    },
    rewardStr: function() {
      return parseFloat(this.userInfo.Reward).toFixed(5);
    },
    dividendStr: function() {
      return parseFloat(this.userInfo.Dividend).toFixed(5);
    }
  }
}
</script>



