
<template>
  <div class="container shifted"><center>
    <h1 class="h1">
      Exchange Gameshare
    </h1>
    
    <table>
      <tr><td>Gameshare count</td><td align="right">{{ lockStr }} </td></tr>
      <tr><td>Gameshare dividend</td><td align="right">{{ dividendStr }} </td></tr>
    </table>

    <p><button class="action" v-if="buyEnabled" v-on:click="buyLock">Buy Gameshare</button></p>
    <p><button class="action" v-if="sellEnabled" v-on:click="sellLock">Sell Gameshare</button></p>
    <button class="action" v-on:click="$router.go(-1)">Close</button>
  </center></div>
</template>

<script>
import Vue from 'vue';
import Amplify, { API } from 'aws-amplify';
import lwiconst from './lwi-const';

export default {
  name: 'XchgLock',
  data () {
    return {
      buyTapped: false
    }
  },

  computed: {
    gameInfo: function() {
      return JSON.parse(sessionStorage.getItem('gameInfo'));
    },
    userInfo: function() {
      return JSON.parse(sessionStorage.getItem('userInfo'));
    },
    buyEnabled: function() {
      return this.userInfo.Diamond > lwiconst.LockBuyPrice;
    },
    sellEnabled: function() {
      return this.userInfo.LockCount > 0;
    },
    lockStr: function() {
      return parseFloat(this.userInfo.LockCount).toFixed(5);
    },
    dividendStr: function() {
      return parseFloat(this.userInfo.Dividend).toFixed(5);
    }
  },
  
  methods: {
    buyLock() {
      this.$router.push('/BuyLock');
    },
    sellLock() {
      this.$router.push('/SellLock');
    }
  }
}
</script>



