
<template>
  <div class="container shifted"><center>
    <h1 class="h1">
      Referral
    </h1>
    <p v-if="card == -1">Loading</p>
    <p v-else-if="card == 0"><button class="action" v-on:click="buildCard">Pay {{ cardBuildPrice }}  to enable others refer me</button></p>
    <p v-else><button class="action" v-on:click="showQR">Share my QRCode to friends</button></p>

    <p v-if="referGift == -1"></p>
    <p v-else-if="referralName == ''"><button class="action" v-on:click="setRefer">Set refer to get {{ referGift }} </button></p>
    <p v-else>My referral: {{ referralName }}</p>

    <button class="action" v-on:click="$router.go(-1)">Close</button>
  </center></div>
</template>

<script>
import Vue from 'vue';
import Amplify, { API } from 'aws-amplify';

export default {
  name: 'Referral',
  data () {
    return {
      card: -1,
      cardBuildPrice: 0,
      referralName: '',
      referGift: -1
    }
  },

  created() {
    API.get('apiLocksmith', '/lwiUserInfo', {}).then(response => {
      console.log(response);
      this.card = response.Card;
      this.cardBuildPrice = response.CardBuildPrice;
      this.referralName = response.ReferralName;
      this.referGift = response.ReferGift
      
    }).catch(error => {
      console.log(error)
    });
  },

  methods: {
    buildCard() {
    },
    showQR() {
      this.$router.push('/qrCode')
    },
    setRefer() {
    }
  }
}
</script>



