
<template>
  <div class="container shifted"><center>
    <h1 class="h1">
      Buy Gameshare
    </h1>
    
    <div v-if="buyTapped">
      <p> Waiting </p>
    </div>

    <table v-else>
      <tr><td>Diamond in wallet</td><td align="right"> {{ balanceStr }} </td></tr>
      <tr><td><input v-model="buyQty" placeholder="Lock count"></td><td align="right"> {{ costStr }} </td>
      <tr><td colspan="2"><button class="action" v-on:click="add1">+1</button><button class="action" v-on:click="add2">+2</button><button class="action" v-on:click="add5">+5</button><button class="action" v-on:click="add10">+10</button><button class="action" v-on:click="add100">+100</button></td></tr>
      <tr><td colspan="2" align="center"><button class="action" v-on:click="buyLock">Buy Gameshare</button></td></tr>
    </table>
    <button class="action" v-on:click="$router.go(-1)">Close</button>
  </center></div>
</template>

<script>
import Vue from 'vue';
import Amplify, { API } from 'aws-amplify';
import lwiconst from './lwi-const';

const uuidv4 = require('uuid/v4');

export default {
  name: 'BuyLock',
  data () {
    return {
      buyQty: 1,
      buyTapped: false
    }
  },

  computed: {
    gameInfo: function() {
      return JSON.parse(sessionStorage.getItem('gameInfo'));
    },
    userInfo: function() {
      return JSON.parse(sessionStorage.getItem('userInfo'));
    },
    balanceStr: function() {
      return parseFloat(this.userInfo.Diamond).toFixed(4);
    },
    costStr: function() {
      return this.cost.toFixed(0);
    },
    cost: function() {
      return this.buyQty * lwiconst.LockBuyPrice;
    }
  },

  created() {
  },

  methods: {
    add1() {
      this.buyQty ++;
    },
    add2() {
      this.buyQty += 2;
    },
    add5() {
      this.buyQty += 5;
    },
    add10() {
      this.buyQty += 10;
    },
    add100() {
      this.buyQty += 100;
    },
    buyLock() {
      if (this.cost > this.userInfo.Diamond) {
        alert('No enough diamond');
        return;
      }

      this.buyTapped = true;
     
      API.post('apiLocksmith', '/lwiBuyLock', {'body': {'Diamond': this.cost, 'TxnId': uuidv4()} }).then(response => {
        this.$router.push('/vault');
      }).catch(error => {
        alert('Something went wrong');
        this.buyTapped = false;
        console.log(error)
      });
    }
  }
}
</script>



