
<template>
  <div class="container shifted">
    <center>
      <h1 class="h1">
        Vault
      </h1>

      <template>
        <div v-if="diamonds == -1">
          Loading
        </div>
        <table v-else>
          <tr><td>Balance</td><td align="right"><button v-on:click="buyDiamond">{{ balanceStr }}  </button></td></tr>
          <tr><td><br><br></td></tr>

          <tr><td>Gamekey count</td><td align="right">{{ gameKeyCountStr }} </td></tr>
          <tr><td>Locked income</td><td align="right">{{ lockedIncomeStr }} </td></tr>
          <tr><td><br><br></td></tr>

          <tr><td>Referral reward</td><td align="right"><button v-on:click="referral">{{ rewardStr }} </button></td></tr>
          <tr><td><br><br></td></tr>

          <tr><td>Gameshare count</td><td align="right"><button v-on:click="xchgLock">{{ lockStr }}  </button></td></tr>
          <tr><td>Gameshare dividend</td><td align="right"><button v-on:click="xchgLock">{{ dividendStr }} </button></td></tr>
          <tr><td><br><br></td></tr>

          <tr><td>Total gain</td><td align="right"><button v-on:click="detail">{{ totalGainStr }} </button></td></tr>
        </table>
      </template>

    </center>
  </div>
</template>

<script>
import Vue from 'vue';
import Amplify, { API } from 'aws-amplify';
import lwiconst from './lwi-const';

export default {
  name: 'Vault',
  data () {
    return {
      diamonds: -1,
      locks: 0,
      keys: 0,
      downs: 0,
      awards: 0,
      dividends: 0,
      rewards: 0,
      lockBuyPrice: 0,

      lockTapped: false
    }
  },

  computed: {
    gameInfo: function() {
      return JSON.parse(sessionStorage.getItem('gameInfo'));
    },
    balanceStr: function() {
      return parseFloat(this.diamonds).toFixed(5);
    },
    gameKeyCountStr: function() {
      return parseFloat(this.keys).toFixed(5);
    },
    rewardStr: function() {
      return parseFloat(this.rewards).toFixed(5);
    },
    lockStr: function() {
      return parseFloat(this.locks).toFixed(5);
    },
    lockedIncomeStr: function() {
      return parseFloat(this.lockedIncome).toFixed(5);
    },
    lockedIncome: function() {
      return this.keys / this.gameInfo.TotalKey * this.gameInfo.ActivePot *lwiconst.LosersRate;
    },
    dividendStr: function() {
      return parseFloat(this.dividends).toFixed(5);
    },
    totalGainStr: function() {
      return parseFloat(this.dividends + this.rewards + this.awards + this.downs + this.lockedIncome).toFixed(5);
    }
  },


  created() {
    API.get('apiLocksmith', '/lwiUserInfo', {}).then(response => {
      sessionStorage.setItem('userInfo', JSON.stringify(response));

      this.diamonds = response.Diamond;
      this.locks = response.LockCount;
      this.keys = response.KeyCount;
      this.downs = response.PotDown;
      this.awards = response.Award;
      this.dividends = response.Dividend;
      this.rewards = response.Reward;

      this.lockBuyPrice = response.LockBuyPrice;
    }).catch(error => {
      console.log(error)
    });
  },

  methods: {
    buyDiamond() {
      this.$router.push('/buyDiamond');
    },
    referral() {
      this.$router.push('/referral');
    },
    xchgLock() {
      this.$router.push('/xchgLock');
    },
    detail() {
      this.$router.push('/detail');
    }
  }
}
</script>



