
<template>
  <div class="container shifted"><center>
    <h1 class="h1">
      Sell Gameshare
    </h1>
    
    <div v-if="sellTapped">
      <p> Waiting </p>
    </div>

    <table v-else>
      <tr><td> Gameshare </td><td align="right"> {{ balanceStr }} </td></tr>
      <tr><td><input v-model="sellQtyStr" placeholder="Gameshare qty"></td><td align="right"> {{ diamondStr }} </td>
      <tr><td colspan="2"><button class="action" v-on:click="div16">1/16</button><button class="action" v-on:click="div8">1/8</button><button class="action" v-on:click="div4">1/4</button><button class="action" v-on:click="div2">1/2</button><button class="action" v-on:click="all">All</button></td></tr>
      <tr><td colspan="2" align="center"><button class="action" v-on:click="sellLock">Sell Gameshare</button></td></tr>
    </table>
    <button class="action" v-on:click="$router.go(-1)">Close</button>
  </center></div>
</template>

<script>
import Vue from 'vue';
import Amplify, { API } from 'aws-amplify';
import lwiconst from './lwi-const';

const uuidv4 = require('uuid/v4');

export default {
  name: 'SellLock',
  data () {
    return {
      sellQty: 1,
      sellTapped: false
    }
  },

  computed: {
    gameInfo: function() {
      return JSON.parse(sessionStorage.getItem('gameInfo'));
    },
    userInfo: function() {
      return JSON.parse(sessionStorage.getItem('userInfo'));
    },
    balanceStr: function() {
      return parseFloat(this.userInfo.LockCount).toFixed(0);
    },
    sellQtyStr: function() {
      return parseFloat(this.sellQty).toFixed(0);
    },
    diamondStr: function() {
      return this.diamond.toFixed(0); 
    },
    diamond: function() {
      return this.sellQty * lwiconst.LockSellPrice;
    }
  },

  created() {
  },

  methods: {
    div16() {
      this.sellQty = Math.trunc(this.userInfo.LockCount / 16);
    },
    div8() {
      this.sellQty = Math.trunc(this.userInfo.LockCount / 8);
    },
    div4() {
      this.sellQty = Math.trunc(this.userInfo.LockCount / 4);
    },
    div2() {
      this.sellQty = Math.trunc(this.userInfo.LockCount / 2);
    },
    all() {
      this.sellQty = this.userInfo.LockCount;
    },
    sellLock() {
      if (this.sellQty <= 0) {
        alert('Qty should be greater than 0');
        return;
      }
      if (this.sellQty > this.userInfo.LockCount) {
        alert('No enough Gameshare to sell');
        return;
      }

      this.sellTapped = true;
     
      API.post('apiLocksmith', '/lwiSellLock', {'body': {'Lock': this.sellQty, 'TxnId': uuidv4()}  }).then(response => {
        this.$router.push('/vault');
      }).catch(error => {
        alert('Something went wrong');
        this.sellTapped = false;
        console.log(error)
      });
    }
  }
}
</script>



