
<template>
  <div class="container shifted">
    <center>
    <h1 class="h1">
      Statistics
    </h1>
    <p>Statistics of all players</p>
    <table>
    <tr><td>Total invested</td><td align="right">{{ invested }} </td></tr>
    <tr><td>Distributed rewards</td><td align="right">{{ rewards }} </td></tr>
    <tr><td>Keys purchased</td><td align="right">{{ keys }} </td></tr>
    <tr><td>Time purchased</td><td align="right">{{ years }} </td></tr>
    <tr><td></td><td align="right">{{ seconds }} </td></tr>
    </table>
    <button class="action" v-on:click="$router.go(-1)">Close</button>
    </center>

  </div>
</template>

<script>
import Vue from 'vue';
import Amplify, { API } from 'aws-amplify';

export default {
  name: 'Stat',
  data () {
    return {
      invested: '',
      rewards: '',
      keys: '',
      years: '',
      seconds: ''
    }
  },

  created() {
    API.get('apiLocksmith', '/lwiInfo', {}).then(response => {
      sessionStorage.setItem('gameInfo', JSON.stringify(response));

      this.invested = parseFloat(response.TotalInvest).toFixed(5) + ' ';
      this.rewards = (response.TotalInvest - response.ActivePot).toFixed(5) + ' ';
      this.keys = parseFloat(response.TotalKey).toFixed(5) + ' ';
      this.years = (response.TotalKey * 30 / (365*24*3600.0)).toFixed(4) + ' years'
      this.seconds = (response.TotalKey * 30).toFixed(2) + ' seconds'
    }).catch(error => {
      console.log(error)
    });

  }
}
</script>



