
<template>
  <div class="container shifted"><center>
    <h1 class="h1">
      {{ message }}
    </h1>
    <div v-if="status == 0">
      Round not opened yet, please wait
    </div>
    <template v-else-if="status == 1">
      <p>{{ info }}</p>
      <button v-on:click="stat">{{ activePotStr }} </button>
      <p>If nobody build new Gamekey in</p>
      <p>{{ timeLeft }}</p>
      <p>Game will reward</p>
      <p>{{ leader }}</p>
      <p>{{ lastWinnerDiamond }}</p>
      <p><button class="action" v-on:click="buildKey">Build Gamekey</button></p>
    </template>
    <div v-else-if="status == 2">
      Round is in batch mode, please wait next round open
    </div>
    <div v-else>
      Loading
    </div>
  </center></div>
</template>

<script>
import Vue from 'vue';
import Amplify, { API } from 'aws-amplify';
import lwiconst from './lwi-const';

let interval = null;

export default {
  name: 'Home',
  data () {
    return {
      message: 'Locksmith',
      status: -1,
      activePot: 0,
      secondsLeft: 0,
      airdropChance: 0,
      airdropPot: 0,
      leaderId: '',
      leader: '',
      info :'Loading'
    }
  },

  computed: {
    activePotStr: function() {
      return parseFloat(this.activePot).toFixed(5); 
    },
    timeLeft: function() {
      return Math.trunc(this.secondsLeft/3600) + ':' + Math.trunc((this.secondsLeft%3600)/60) + ':' + Math.trunc((this.secondsLeft%3600)%60);
    },
    lastWinnerDiamond: function() {
      return this.activePot * lwiconst['WinnerRate'];
    }
  },

  async created() {
    const currentUser = await this.$Amplify.Auth.currentUserInfo();

    API.get('apiLocksmith', '/lwiInfo', {}).then(response => {
      sessionStorage.setItem('gameInfo', JSON.stringify(response));

      this.status = parseInt(response.Status);
      this.message = 'Locksmith round ' + (1+response.Round);

      if (this.status == 1) {
        this.activePot = response.ActivePot;
        this.secondsLeft = response.DrainTime - Math.trunc((new Date()).getTime() / 1000);
        this.airdropChance = response.AirdropChance;
        this.airdropPot = response.AirdropPot;
        this.leaderId = response.LeaderId;
        this.leader = response.LeaderName;

        interval = setInterval(() => {
          if (this.secondsLeft > 1) {
            this.secondsLeft --;
          }
        },1000);

        if (this.leaderId == currentUser.id) {
          this.info = 'You are current winner!';
        } else {
          this.info =  'You are NOT the current winner. Build Gamekey to make you as the current winner.';
        }
      }
    }).catch(error => {
      console.log(error)
    });
  },

  destroyed() {
    clearInterval(interval);
  },

  methods: {
    stat() {
      this.$router.push('/stat');
    },
    buildKey() {
      this.$router.push('/buildKey');
    }
  }
}
</script>



