
<template>
  <div class="container shifted"><center>
    <h1 class="h1">
      Buy Diamond
    </h1>
    
    <div v-if="buyTapped">
      <p> Waiting </p>
    </div>

    <table v-else>
      <tr><td> 1,000 Diamonds</td><td align="right"> <button class="action" v-on:click="buyT1">$0.99</button> </td></tr>
     <tr><td> 10,000 Diamonds</td><td align="right"> <button class="action" v-on:click="buyT10">$9.99</button> </td></tr>
    </table>
    <button class="action" v-on:click="$router.go(-1)">Close</button>
  </center></div>
</template>

<script>
import Vue from 'vue';
import Amplify, { API } from 'aws-amplify';

const uuidv4 = require('uuid/v4');

export default {
  name: 'BuyDiamond',
  data () {
    return {
      buyTapped: false
    }
  },

  computed: {
    gameInfo: function() {
      return JSON.parse(sessionStorage.getItem('gameInfo'));
    },
    userInfo: function() {
      return JSON.parse(sessionStorage.getItem('userInfo'));
    }
  },

  created() {
  },

  methods: {
    buyT1() {
      alert('The feature is under construction, coming soon');
    },
    buyT10() {
      alert('The feature is under construction, coming soon');
    }
  }
}
</script>



